.link-button {
    display: inline-block;
    position: relative;
}

.link-button::before {
    display: block;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    bottom: 0;
    transition: width 0.35s ease-out;
    pointer-events: none;
}

.card1 .link-button::before {
    background: rgba(250, 128, 114, 0.4);
}

.card2 .link-button::before {
    background: rgba(109, 69, 62, 0.4);
}

.card3 .link-button::before {
    background: rgba(64, 53, 49, 0.4);
}

.card4 .link-button::before {
    background: rgba(90, 88, 88, 0.4);
}

.link-button:hover::before {
    width: 100%;
}

.link-button a {
    position: relative;
    display: block;
    color: inherit;
    padding: 8px;
    z-index: 1000;
}