#footer {
    padding-top: calc(6vw + 6vw);
    padding-left: 10vw;
    padding-right: 10vw;
    color: #737373;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
}

.footer-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    vertical-align: center;
    padding-bottom: 2em;
}

#footer h5 {
    margin: 0 0 0 1em;
}

#footer h5::after {
    content: "|";
    margin-left: 1vw;
}

#footer h5:last-child::after {
    content: "";
}

#footer hr {
    margin-bottom: 2em;
    border: none;
    background: #737373;
    height: 1px;
}