.card {
    position: relative;
    background: var(--card-bg);
    margin: 0;
    padding: 0 0 var(--triangle-height);
    display: flex;
    align-items: center;
    min-height: var(--card-min-height);
}

.card1 {
    --card-bg: #AD847D;
}

.card2 {
    --card-bg: #856A64;
}

.card3 {
    --card-bg: #5C4F4C;
}

.card4 {
    --card-bg: #343434;
}

.last-card {
    padding-bottom: 0;
}

.card-content {
    margin-left: 10vw;
    margin-right: 10vw;
    display: flex;
    width: 100%;
    box-sizing: border-box;
    padding: var(--card-hpadding) 0;
}

.card-text {
    flex: 1;
}

@media(max-width: 700px) {
    .card-content {
        flex-direction: column;
        min-height: var(--card-min-height);
    }

    .card-image {
        display: flex;
        flex: 3;
        margin: 0 0 2em;
    }

    .card-text {
        flex: 2;
    }

    .card-image img {
        width: 140px;
        height: 140px;
    }
}