.card-rtl .email {
    justify-content: end;
}

.card-rtl .card-content {
    flex-direction: row-reverse;
}

.card-rtl .card-image {
    margin-left: var(--card-gap);
    margin-right: 0;
}

.card-rtl .card-content {
    text-align: right;
}

@media(max-width: 700px) {
    .card-rtl .card-content {
        flex-direction: column;
        min-height: var(--card-min-height);
    }

    .card-rtl .email {
        justify-content: start;
    }

    .card-rtl .card-content {
        text-align: left;
    }

    .card-rtl .card-image {
        margin-left: 0;
        margin-right: 0;
    }
}