.triangle-up {
    position: absolute;
    top: calc(-1 * var(--triangle-height));
    left: 0;
    right: 0;
    width: 100vw;
    height: var(--triangle-height);
    transform: translateY(1px);
}

.triangle-bottom {
    position: absolute;
    bottom: calc(-1 * var(--triangle-height));
    left: 0;
    right: 0;
    width: 100vw;
    height: var(--triangle-height);
    z-index: 10;
    transform: translateY(-1px);
}

.triangle-fill {
    fill: var(--card-bg);
}