.icons-container {
    text-align: center;
    margin: 0 auto 5em;
}

.icon {
    width: var(--icon-size);
    height: var(--icon-size);
    border-radius: 30%;
    border: solid 2px white;
    padding: 0.5em;
    margin: 0 2.5em;
}

@media(max-width: 700px) {
    .icons-container {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    .icon {
        margin: 0 0.5em;
    }
}