.card-image {
    margin-right: var(--card-gap);
    display: flex;
    align-items: center;
}

.card-image img {
    width: var(--card-image-size);
    height: var(--card-image-size);
}

.card-image img {
    margin: 0 auto;

    opacity: 0.9;
    transition: opacity 0.2s linear, transform 0.2s ease-out;
}

.card-image img:hover {
    opacity: 1;
    transform: scale(1.1);
}

.rounded {
    object-fit: cover;
    border-radius: 50%;
}