:root {
  --card-min-height: 500px;
  --triangle-height: 6vw;
  --card-gap: 15vw;
  --card-image-size: 276px;
  --card-hpadding: 2em;
  --icon-size: 50px;
}

body {
  margin: 0;
  padding: 0;
  background: black;
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
}

hr {
  height: 1px;
  background-color: white;
  border: none;
}

h1, h2, h3, h4, h5, .link-button {
  font-family: 'Montserrat', sans-serif;
}

p {
  font-size: 1.1em;
}

@media(max-width: 1000px) {
  body {
    --card-gap: 8vw;
    --card-image-size: 220px;
  }
}

@media(max-width: 700px) {
  body {
    --card-min-height: 600px;
    --card-image-size: 150px;
    --card-hpadding: 2em;
  }
}